import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Inject, Injector, Input, Output } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

import { NGX_GOOGLE_PLACES_AUTOCOMPLETE_CONFIG, NgxGooglePlacesAutocompleteConfig } from './ngx-google-places-autocomplete.config';
import { NgxGooglePlacesAutocompleteControlValueAccessor } from './ngx-google-places-autocomplete.control-value-accessor';
import { Prediction } from './ngx-google-places-autocomplete.models';

@Component({
  providers: [{ provide: NG_VALUE_ACCESSOR, useExisting: NgxGooglePlacesAutocompleteComponent, multi: true }],
  selector: 'ngx-google-places-autocomplete',
  templateUrl: './ngx-google-places-autocomplete.component.html'
})
export class NgxGooglePlacesAutocompleteComponent extends NgxGooglePlacesAutocompleteControlValueAccessor {
  private service: google.maps.places.AutocompleteService;

  public index: number = -1;
  public predictions: Prediction[] = [];
  public show: boolean = false;

  @Input() public placeholder: string = 'Enter a location';
  @Output() public scriptLoaded: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private elementRef: ElementRef,
    injector: Injector,
    @Inject(NGX_GOOGLE_PLACES_AUTOCOMPLETE_CONFIG) private ngxGooglePlacesAutocompleteConfig: NgxGooglePlacesAutocompleteConfig
  ) {
    super(injector);
    this.initialize().then(() => this.service = new google.maps.places.AutocompleteService());
  }

  public onBlur(): void {
    this.reset(true);
  }

  public onClick(): void {
    this.control.setValue(null);
  }

  public onFocus(): void {
    this.reset(true, true);
  }

  public onKeyDown(event: KeyboardEvent): void {
    if (!this.show) return;
    switch (event.key) {
      case 'ArrowDown': this.index += 1; if (this.index >= this.predictions.length) this.index = this.predictions.length - 1; break;
      case 'ArrowUp': this.index -= 1; if (this.index < 0) this.index = 0; break;
      case 'Escape': this.reset(true); break;
      case 'Enter': this.index !== -1 ? this.onMouseDown(this.predictions[this.index]) : this.reset(true); break;
    }
  }

  public onModelChange(input: string): void {
    if (!input) { this.reset(); return; }
    const request = Object.assign({ input }, this.ngxGooglePlacesAutocompleteConfig.options);
    this.service.getPlacePredictions(request, (predictions: Prediction[], status: google.maps.places.PlacesServiceStatus) => {
      if (status !== google.maps.places.PlacesServiceStatus.OK) { this.reset(); return; }
      this.predictions = predictions;
      this.show = true;
      this.changeDetectorRef.detectChanges();
    });
  }

  public onMouseDown(prediction: Prediction, event?: MouseEvent): void {
    this.control.setValue(prediction.description, { emitViewToModelChange: false });
    if (event) this.dispatchEvent(event);
    this.reset(true);
  }

  private dispatchEvent(event: MouseEvent): void {
    let customEvent: CustomEvent;
    if (window.CustomEvent) {
      customEvent = new CustomEvent('submit', { bubbles: true, cancelable: true, detail: event });
    } else {
      customEvent = document.createEvent('CustomEvent');
      customEvent.initCustomEvent('submit', true, true, { data: event });
    }
    this.elementRef.nativeElement.firstElementChild.firstElementChild.form.dispatchEvent(customEvent);
  }

  private async initialize(): Promise<void> {
    if (window.google?.maps) return new Promise((resolve) => resolve());
    return new Promise((resolve, reject) => {
      window['ngxGooglePlacesAutocompleteCallback'] = () => { this.scriptLoaded.emit(true); resolve(); };
      const script = document.createElement('script');
      script.async = true;
      script.defer = true;
      script.onerror = (err) => reject(err);
      script.src = `https://maps.googleapis.com/maps/api/js?callback=ngxGooglePlacesAutocompleteCallback&key=${this.ngxGooglePlacesAutocompleteConfig.key}&language=${this.ngxGooglePlacesAutocompleteConfig.language}&libraries=${this.ngxGooglePlacesAutocompleteConfig.libraries}&region=${this.ngxGooglePlacesAutocompleteConfig.region}`;
      script.type = 'text/javascript';
      document.body.appendChild(script);
    });
  }

  private reset(keep: boolean = false, show: boolean = false): void {
    this.index = -1;
    if (!keep) this.predictions = [];
    this.show = show && this.predictions.length > 0;
  }
}