import { Injector, Input, ViewChild } from '@angular/core';
import { AbstractControl, ControlContainer, ControlValueAccessor, FormControl, FormControlDirective } from '@angular/forms';

export class NgxGooglePlacesAutocompleteControlValueAccessor implements ControlValueAccessor {
  @Input() public formControl: FormControl;
  @Input() public formControlName: string;

  @ViewChild(FormControlDirective, { static: true }) public formControlDirective: FormControlDirective;

  constructor(private injector: Injector) {}

  public get control(): AbstractControl {
    return this.formControl || this.controlContainer.control.get(this.formControlName);
  }

  public get controlContainer(): ControlContainer {
    return this.injector.get(ControlContainer);
  }

  public registerOnChange(fn: any): void {
    this.formControlDirective.valueAccessor.registerOnChange(fn);
  }

  public registerOnTouched(fn: any): void {
    this.formControlDirective.valueAccessor.registerOnTouched(fn);
  }

  public setDisabledState(isDisabled: boolean): void {
    this.formControlDirective.valueAccessor.setDisabledState(isDisabled);
  }

  public writeValue(obj: any): void {
    this.formControlDirective.valueAccessor.writeValue(obj);
  }
}