import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { ReactiveFormsModule } from '@angular/forms';

import { NgxGooglePlacesAutocompleteComponent } from './ngx-google-places-autocomplete.component';
import { NGX_GOOGLE_PLACES_AUTOCOMPLETE_CONFIG, NgxGooglePlacesAutocompleteConfig } from './ngx-google-places-autocomplete.config';
import { NgxGooglePlacesAutocompletePipe } from './ngx-google-places-autocomplete.pipe';

@NgModule({
  declarations: [NgxGooglePlacesAutocompleteComponent, NgxGooglePlacesAutocompletePipe],
  exports: [NgxGooglePlacesAutocompleteComponent],
  imports: [CommonModule, ReactiveFormsModule]
})
export class NgxGooglePlacesAutocompleteModule {
  public static forRoot(ngxGooglePlacesAutocompleteConfig: NgxGooglePlacesAutocompleteConfig): ModuleWithProviders<NgxGooglePlacesAutocompleteModule> {
    return {
      ngModule: NgxGooglePlacesAutocompleteModule,
      providers: [{ provide: NGX_GOOGLE_PLACES_AUTOCOMPLETE_CONFIG, useValue: ngxGooglePlacesAutocompleteConfig }]
    };
  }
}